.contacts {
  background: #f8f9fa;
  border-radius: 10px;
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

.contacts__form {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.contacts__input {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  transition: all .3s ease-in-out;
}

.contacts__input:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 5px #007bff80;
}

.contacts__button {
  color: #fff;
  cursor: pointer;
  background: #ea6f10;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  transition: all .3s;
}

.contacts__button:hover {
  background: #ac510c;
}

.contacts__list {
  margin-top: 20px;
  padding: 0;
}

.contacts__item {
  background: #fff;
  border-radius: 5px;
  flex-direction: column;
  gap: 5px;
  margin-bottom: 10px;
  padding: 15px;
  transition: all .3s;
  display: flex;
  position: relative;
  box-shadow: 0 2px 5px #0000001a;
}

.contacts__delete, .contacts__modify {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 14px;
  transition: all .3s;
  position: absolute;
  top: 10px;
}

.contacts__delete {
  color: #fff;
  background: #dc3545;
  right: 10px;
}

.contacts__delete:hover {
  background: #c82333;
}

.contacts__modify {
  color: #000;
  background: #ffc107;
  right: 45px;
}

.contacts__modify:hover {
  background: #e0a800;
}

.contacts__name {
  margin: 0 0 5px;
  font-size: 18px;
  font-weight: bold;
}

.contacts__tel, .contacts__email {
  color: #555;
  font-size: 14px;
}

.hidden {
  display: none;
}
